.content {
  /* position: relative; */
}

.content h1 {
  /* color: rgb(7, 7, 7); */
  font-size: 1.5em;
  /* position: absolute; */
  /* transform: translate(-50%, -50%); */
}

.content h1:nth-child(1) {
  color: transparent;
  -webkit-text-stroke: 2px #90f5cd;
}

.content h1:nth-child(2) {
  color: #0307f4;
  animation: animate 4s ease-in-out infinite;
}

@keyframes animate {
  0%,
  100% {
    clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
  }

  50% {
    clip-path: polygon(
      0% 60%,
      15% 65%,
      34% 66%,
      51% 62%,
      67% 50%,
      84% 45%,
      100% 46%,
      100% 100%,
      0% 100%
    );
  }
}